import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import logo from '../../Assets/HomeImg/cel/Logo_white.png'
import { ShoppingCart } from '@material-ui/icons';
import { Badge } from '@material-ui/core';
import { Link } from 'react-router-dom';
//import React, { useContext } from 'react';
//import StateContext from '../../App.js'




const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginBottom: "7rem",
  },

  appBar:{
      backgroundColor:"black",
  },
  
  grow:{
      flexGrow:1,
  },

  button:{
      marginLeft: theme.spacing(2),
  },

  image: {
    marginRight: "10px", 
    height:"5rem",
    width:"10rem",},
}));

export default function Navbar({items,state}) {
  const classes = useStyles();
  console.log(state)

  return (
    <div className={classes.root}>
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>

          <Link to="/">
          <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <img className={classes.image} src={logo} alt="logo"/>
          </IconButton>
          </Link>

          <div className={classes.grow}/>
          
          <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>

          <Link to="/checkout-page">
          <IconButton className={classes.button} aria-label="show cart items" color="inherit" >
            <Badge badgeContent={items} color="secondary">
            <ShoppingCart fontSize="large" color="white"></ShoppingCart>
            </Badge>
          </IconButton>
          </Link> 


        </Toolbar>
      </AppBar>
    </div>
  )
}
