import React, { useReducer } from 'react'
import { Switch, BrowserRouter as Router,Route } from 'react-router-dom';
import './App.css';
import Navbar from './Components/Nav/Nav';
import Premium from './Components/Premium/Premium.jsx'
import CheckoutPage from './Components/SalesCar/CheckoutPage';
import {reducer, actionTypes, initialState } from './reducer';


export const StateContext = React.createContext();
//export const CartContext = React.createContext()


function App() {

  const [state, dispatch] = useReducer(reducer, initialState)
  console.log(state)
 
  const addToBasket = (name="hola",id="5",category,price,img,description) =>{
    dispatch({
      type: actionTypes.ADD_TO_BASKET,

      item:{
        name:name,
        id:id,
        category:category,
        price,
        img,
        description,

        //Both nomenclature are correct
      }
    
    })}
  
      const removeItem = (id) => {
        dispatch({
          type: actionTypes.REMOVE_ITEM,
          id: id,
        });
      }; 
  
  const items = state.basket.length
  console.log(items)
  


  return (
    <StateContext.Provider value={{state:state,dispatch,removeItem,addToBasket}}>
   {/*  <CartContext.Provider value={{ cartState: state, cartDispatch: dispatch, addToCart, delFromCart, clearCart }}> */}


    <Router>

    <div className="App">
      {/* <Home />  */}
      {/* <Header />   */}
      {/* <Product/> */}
       <Navbar items={items} state={state}/> 
      <Switch>
        <Route path="/checkout-page">
          <CheckoutPage state={state}/>
        </Route>

        <Route path="/">
          <Premium/>
        </Route>

      </Switch>
       
       {/* <Normal/>  */}
       

    </div>

    </Router>

    </StateContext.Provider>
  );
}

export default App;


